/* General */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

img {
  display: block;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #000;
}

.container {
  width: 100%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  transition: background ease-in 250ms;
  z-index: 1;
}

/* Header */

.header {
  min-height: 50px;
  height: 10vh;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.75);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.125);
}

.header h1 {
  margin-left: 1rem;
  font-size: 1.5rem;
  color: #222;
}

.nav {
  height: 100%;
}

.nav__list {
  height: 100%;
  margin-right: 2rem;
  display: flex;
  align-items: center;
  position: static;
}

.nav__item {
  padding: 0 1rem;
}

/* Header End */

.title__box {
  text-align: center;
  font-weight: 700;
}

.title__color-name {
  font-size: 3vw;
}

.title__box p:hover {
  opacity: 0.7;
  cursor: pointer;
}

button {
  display: inline-block;
  margin-top: 2.5em;
  padding: 1.5em;
  border: none;
  background: transparent;
  border: 4px solid black;
  border-radius: 3px;
  cursor: pointer;
  font-size: 1.3em;
  font-weight: bold;
}

.generate-btn {
  margin-right: 1rem;
}

button:hover {
  background: #fff;
}

button:focus {
  outline: none;
}

/* Favourites menu */
.favourites {
  max-width: 90vw;
  max-height: 700px;
  position: absolute;
  right: 0;
  top: 16vh;
  background: rgba(255, 255, 255, 0.85);
  bottom: 1rem;
  transition: all ease-in-out 0.25s;
  transform: translateX(100%);
}
.favourites.open {
  transform: translateX(0%);
}

.favourites.open .f-toggle img {
  transform: rotate(180deg);
}

.f-toggle {
  position: absolute;
  left: -32px;
  width: 32px;
  height: 32px;
  /* top: 10px; */
  background: #fff;
  border-radius: 5px 0 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.f-toggle img {
  height: 15px;
  width: 15px;
  transform: rotate(180deg);
  transition: transform ease 0.25s;
}

.favourites.open .f-toggle img {
  transform: rotate(360deg);
}

.f-toggle:hover {
  opacity: 0.85;
  cursor: pointer;
}

.fav-color {
  position: relative;
}

#list {
  overflow: scroll;
  max-height: 80vh;
}

#list .list-inner {
  margin-top: 2em;
  list-style-type: none;
}

li {
  padding: 1.25em 5.5em;
  background: #fff;
  font-weight: bold;
}

li:hover {
  cursor: pointer;
  opacity: 0.8;
}

#input {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.remove {
  width: 30px;
  height: 30px;
  position: absolute;
  left: 20px;
  background: url("images/error.svg") no-repeat center;
  line-height: 0;
  border-radius: 25%;
  top: calc(50% - 15px);
}

.remove:hover {
  opacity: 0.5;
}

.list-inner.no-border {
  border: none;
}

/* Other Pages */

.about-page {
  width: 80%;
  margin: 0 auto;
  padding-top: 2rem;
}

@media screen and (max-width: 900px) {
  .title__color-name {
    font-size: 24px;
  }
}

@media screen and (max-width: 600px) {
  .buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
